import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './index.scss'

export default class Solution extends Component {
  constructor() {
    super()
    this.state = {
      active: 0
    }
    this.contents = [{
      title: '',
      img: require('../../../../assets/images/home/solution-banner-bg1.png'),
      descr: 'OBASE ',
      extra: 1
    }, {
      title: '',
      img: require('../../../../assets/images/home/solution-banner-bg2.png'),
      descr: '3.55000W(1insert+2update+5select)db21500TPSOBASE 17000TPS12'
    }, {
      title: '',
      img: require('../../../../assets/images/home/solution-banner-bg3.png'),
      descr: 'TPS1600 ;OBASETPS150002017TPS35000;MIPS '
    }, {
      title: '',
      img: require('../../../../assets/images/home/solution-banner-bg4.png'),
      descr: ';OBASEO'
    }]
  }

  get currItem() {
    return this.contents[this.state.active]
  }

  handleClick(index) {
    this.setState({active: index})
  }

  render() {
    const navs = ['', '', '', '']
    return (
      <div className="solution">
        <div className="container">
          <div className="sub-title"></div>
          <div className="navs">
            { navs.map((nav,index) =>
              <div key={nav}
                className={this.state.active === index ? 'nav active': 'nav'}
                onClick={this.handleClick.bind(this, index)}
              >{nav}</div>) }
          </div>
          <div className="content">
            <div className="nav1-content">
              <div className="info">
                <div className="title">{this.currItem.title}</div>
                <div className="descr">{this.currItem.descr}</div>
                {
                  this.currItem.extra && (
                    <div className="data">
                      <div className="data-item">
                        <div className="num">100TB</div>
                        <div className="txt"></div>
                      </div>
                      <div className="data-item">
                        <div className="num">585</div>
                        <div className="txt"></div>
                      </div>
                    </div>
                  )
                }
                <Link to="/solution" className="more"></Link>
              </div>
              <div className="img">
                <img src={this.currItem.img} alt=""/>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}