import React, { Component } from 'react'
import { NavLink, Link } from 'react-router-dom'
import './header.scss'

export default class About extends Component {
  constructor() {
    super()
    this.navs = [
      { path: '/', label: '' },
      { path: '/production', label: '' },
      { path: '/solution', label: '' },
      { path: '/document', label: '' },
      { path: '/about', label: '' },
    ]
  }

  render() {
    return (
      <div className='header'>
        <header className="container">
          <Link to="/"><h1 className="logo"></h1></Link>
          <ul className="nav">
            {this.navs.map(nav => 
              <li className="nav-item" key={nav.path}>
                <NavLink exact activeClassName="active" className="link" to={nav.path}>{nav.label}</NavLink>
              </li>
            )}
          </ul>
        </header>
      </div>
    )
  }
}